




//- иконки
%icon {
	display: inline-block;
	vertical-align: middle;
	width: 1em;
	height: 1em;
}


%background-icon {
	background-position: center center;
	background-repeat: no-repeat;
	background-size: contain;
	max-width: 512px;
	max-height: 512px;
}


%icon-element {
	display: inline-block;
	width: 1em;
	height: 1em;
	vertical-align: middle;
	margin-right: 0.3em;
}



%icon-double-arrow { background-image: url( 'data:image/svg+xml;base64,' ); }
%icon-play { background-image: url( 'data:image/svg+xml;utf8;base64,' ); }


