











.btn {

	@extend %button;

	&-default { @extend %button-default; }

	&-primary { @extend %button-primary; }

	&-success { @extend %button-success; }

	&-warning { @extend %button-warning; }

	&-info { @extend %button-info; }

	&-block { display: block; }

	&-sm { @extend %button-small; }

	&-lg { @extend %button-large; }

}
