





.accordio {


	.listitem {
		margin: 1em 0;
	}

	&__listitem {
		.panel {
			position: relative;
			background-color: $titles-color;
			cursor: pointer;
			min-height: 3em;
			padding: 0.5em;
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: flex-start;
			transform-origin: center center;
			transition: background-color 200ms;
		}
		.panel:hover { background-color: $titles-color; }
		.panel .thumbnail {
			position: absolute;
			top: -0.2em;
			left: -0.2em;
			width: 3.5em;
			height: 3.5em;
			border: 2px solid $titles-color;
			background-color: #f8f8f8;
			padding: 0.5em;
		}
		.panel:after {
			@extend %background-icon;
			content: '';
			margin-left: auto;
			margin-right: 1em;
			background-image: url( 'data:image/svg+xml;base64,' );
			width: 1em;
			height: 1em;
			transform-origin: center center;
			transition: transform 200ms;
			flex-shrink: 0;
		}
		.panel .title {
			font-size: 1em;
			line-height: 1.5em;
			font-weight: bold;
			color: #fff;
			text-shadow: 1px 1px 1px rgba( 0, 0, 0, 0.75 );
			margin-top: 0;
			margin-bottom: 0;
			font-variant: normal;
		}
		.panel .thumbnail + .title {
			margin-left: 4.5em;
		}
		.content {
			background-color: #f8f8f8;
			padding: 0.5em;
			border: 1px solid $titles-color;
		}
	}

	&__listitem.active {
		.panel { background-color: $titles-color; }
		.panel:after { transform: rotate( 180deg ); }
	}


}


.accordio.accordio--primary {
	.accordio__listitem .panel { background-color: lighten( $brand-primary, 15% ); }
	.accordio__listitem .panel:hover { background-color: lighten( $brand-primary, 15% ); }
	.panel .thumbnail { border: 2px solid $brand-primary; }
	.content { border: .1em solid $brand-primary; }
}





$accordio_variants: (
	"primary": darken( $brand-primary, 25% ),
	"success": $brand-success,
	"warning": $brand-warning,
	"info":    $brand-info,
);


@each $name, $color in $accordio_variants {
	.accordio.is-style-accordio-#{$name},
    .accordio.accordio--#{$name} {
		.accordio__listitem .panel { background-color: $color; }
		.accordio__listitem .panel:hover { background-color: lighten( $color, 10% ); }
		.accordio__listitem.active .panel { background-color: lighten( $color, 10% ); }
		.panel .thumbnail { border: .1em solid $color; }
		.content { border: .1em solid $color; }
	}
}