

%file {
  display: inline-block;
  width: 1.3em;
  height: 1.3em;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  vertical-align: middle;
  margin-right: 5px;
}


%file-doc { background-image: url( 'data:image/svg+xml;utf8;base64,' ); }
%file-xls { background-image: url( 'data:image/svg+xml;utf8;base64,' ); }
%file-ppt { background-image: url( 'data:image/svg+xml;utf8;base64,' ); }
%file-zip { background-image: url( 'data:image/svg+xml;utf8;base64,' ); }
%file-pdf { background-image: url( 'data:image/svg+xml;base64,' ); }
%file-odt { background-image: url( 'data:image/svg+xml;base64,' ); }
