/**
 *  Навигационные меню
 */


.nav {

	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	position: relative;
	background-color: $brand-info;


	ul {
		margin-top: 0;
		margin-bottom: 0;
	}
	


	.list {
		margin: 0;
		padding-left: 0;
		list-style-type: none;
		display: none;
		align-items: stretch;
		@include min-width( $screen-lg ) {
			display: flex;
		}
	}



	&__list {

		li {
			margin: 0 .5em;
			&:hover { background-color: darken( $brand-info, 10% ); }
		}

		
		> li > a {
			display: block;
			padding: .5em 1em;
		}
		

		> li > .sub-menu {
			display: none;
			list-style-type: none;
		}


		> .has-sub-menu > a:after {
			@extend %icon;
			@extend %background-icon;
			content: '';
			background-image: url( 'data:image/svg+xml;base64,' );
			margin-left: .5em;
			font-size: 85%;
		}

		> .has-sub-menu:hover > .sub-menu {
			position: absolute;
			padding: .5em 1em;
			top: 100%;
			left: 0;
			right: 0;
			z-index: 1000;
			display: flex;
			flex-direction: column;
			max-height: 20em;
			background-color: darken( $brand-info, 10% );
			flex-wrap: wrap;
		}

		> .has-sub-menu.column {
			position: relative;
		}

		> .has-sub-menu.column:hover > .sub-menu {
			right: auto;
			width: 280px;
		}

		.has-sub-menu .has-sub-menu > a {
			font-weight: bold;
			font-size: 100%;
		}

		.sub-menu .sub-menu a {
			font-size: 85%;
		}

		.sub-menu a:hover {
			text-decoration: underline;
		}

		.sub-menu .sub-menu {
			display: block;
		}

	}




	


	.burger {
		padding: 0;
		border: none;
		margin: 0 0 0 auto;
		background-color: transparent;
		position: relative;
		padding: .5em 1em;
		transform-origin: center center;
		@include min-width( $screen-lg ) {
			display: none;
		}
	}


	.burger::after {
		@extend %icon;
		@extend %background-icon;
		content: '';
		background-image: url( 'data:image/svg+xml;base64,' );
		width: 1.5em;
		height: 1.5em;
		margin-left: 1em;
		transition: transform .2s;
		transform-origin: center center;
	}


	.burger--active {
		z-index: 1000;
		transform: scale( 1.1 );
		background-color: $brand-success;
		box-shadow: $block-shadow;
	}


	.burger--active::after {
		transform: rotate( 90deg );
	}
	


	.languages {
		list-style-type: none;
	}


	&__languages {
		a { padding: .5em; }
		.current { padding: .5em; background-color: rgba( 255, 255, 255, .1 ); }
		li { display: inline-block; }
	}

	

}