







.mobilenav {

	position: fixed;
	top: 0%;
	left: 100%;
	bottom: 0%;
	width: 260px;
	padding: 1em;
	z-index: 1000;
	background-color: #fff;
	overflow-x: hidden;
	overflow-y: auto;
	transition: transform .2s;
	transform: translate( 0%, 0% );


	&--active {
		transform: translate( -100%, 0% );
	}



	.close {
		@extend  %background-icon;
		background-image: url( 'data:image/svg+xml;utf8;base64,' );
		display: block;
		width: 2em;
		height: 2em;
		border: none;
		background-color: transparent;
		float: right;
		margin: .5em;
		padding: 0;
	}



}